div,h1,h2,h3,h4,h5,h6,p,li,a,span,strong {
	overflow-wrap: normal;
    word-wrap: normal;
    -ms-word-break: normal;
    word-break: normal;
    word-break: normal;
    -ms-hyphens: none;
    -moz-hyphens: none;
    -webkit-hyphens: none;
    hyphens: none;
    page-break-inside: avoid;
}
* {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}
pre {
	font-family: $font_pre;
	font-size: 13px;
	line-height: 1.3;
}
a {
	color: $color_link;
	&:hover {
		color: tint($color_link,50%);
	}
}
a,input,button {
	transition: all ease-out .3s;
}
.cf:before, .cf:after {
    content: " "; 
    display: table; 
}
.cf:after { clear: both; }
.cf { *zoom: 1; }
.text-center {text-align:center;}
@media screen and (max-width: 600px) {
	#wpadminbar {
		top: -46px;
	}
}

/* Text meant only for screen readers. */
.sr {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	&:focus {
		background-color: #333;
		border-radius: 3px;
		box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
		clip: auto !important;
		color: #FFF;
		display: block;
		@include font-size(0.875);
		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000; /* Above WP toolbar. */
	}
}
body {
	font-family: $font_main;
	font-size: 15px;
	font-weight: 300;
	line-height: 1.6;
	color: $color_text_main;
	position: relative;
}
h1,h2,h3,h4,h5,h6 {line-height:1.4}
h1 {font-size:2rem;font-weight:500;}
h2 {font-size:1.3rem;font-weight:500;}
h3 {font-size:1.3rem;font-weight:500;}
h4 {font-size:1.1rem;font-weight:500;}
h5 {font-size:0.95rem;font-weight:500;}
h6 {font-size:0.85rem;font-weight:500;}
.wrapper {
	max-width: $wrapper_width;
	width: 100%;
	margin: 0 auto;
	padding-left: 30px;
	padding-right: 30px;
}
.site {
	position: relative;
	overflow-x: hidden;
    overflow-y: auto;
}
#content {
	margin-top: 20px;
}
.default .site-main {
	min-height: 30em;
}
.btn-default {
	background: $primary;
	display: inline-block;
	text-decoration: none;
	-webkit-apperance:none;
	box-shadow: none;
	text-shadow: none;
	border: none;
	cursor: pointer;
	color: #FFF;
