/* Header */
.site-header {
	position: relative;
}
.logo {
	display: block;
	width: 75px;
	float: left;
	@include mid {
		width: 220px;
		box-shadow: 1px 1px 5px 2px rgba(0,0,0,0.08);
	}
	a {
		background: #FFF;
		padding: 12px 0 16px;
		@include mid {
			padding: 18px 35px 20px;
		}
	}
	a, img {
		display: block;
		width: 100%;
		height: auto;
		float: left;
	}
}
.logo-wrapper {
	position: relative;
}
/* Navigation */
.main-navigation {
	@include mid {
		width: 80%;
		position: absolute;
		top: 0;
		right: 0;
		margin-top: 30px;
	}
	@include desk {
		width: 70%;
	}

	.main-menu-wrap {
		transition: all ease-in-out .5s;
		@include mid {
			background: -moz-linear-gradient(left,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 20%, rgba(255,255,255,1) 100%);
			background: -webkit-linear-gradient(left,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 20%,rgba(255,255,255,1) 100%);
			background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 20%,rgba(255,255,255,1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=1 );
			box-shadow: 10px 1px 10px 2px rgba(0, 0, 0, 0.05);
			display: inline-block;
			float: right;
			padding-left: 6%;
			padding-right: 6%;
			height: auto;
			overflow: visible;
		}
		@include desk {
			padding-left: 10%;
			padding-right: 15%;
		}
	}
	ul {
		margin: 0 0;
		padding: 0 0;
		list-style: none;
	}
	ul.menu {
		display: block;
		width: 100%;
		float: left;
		li {
			position: relative;
		}
	}
	ul.menu > li {
		@include tab {
			display: inline-block;
			float: left;
		}
	}
	ul.menu a {
		display: block;
		text-decoration: none;
		position: relative;
		span {
			position: relative;
			z-index: 5;
		}
	}
	ul.menu > li > a {
		padding: 14px 20px;
		font-size: 16px;
		font-weight: 400;
		color: $color_menu_link;
		@include mid {
			font-weight: 300;
			&:before {
				content:"";
				display: block;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				opacity: 0;
				background: $primary;
				transition: all ease-in-out .3s;
				transform: scaleX(0);
			}
		}
	}
	ul.menu > li.current-menu-item > a,
	ul.menu > li.current_page_item > a {
		background: $primary;
		color: #FFF;
		@include mid {
			&:before {
				opacity: 1;
				width: 100%;
				transform: scaleX(1);
			}
		}
	}
	ul.menu > li:hover > a {
		@include mid {
			background: transparent;
			color: #FFF;
			&:before {
				opacity: 1;
				width: 100%;
				transform: scaleX(1);
			}
		}
	}

	/* Dropdown */
	ul.menu ul {
		li {
			position: relative;
			z-index: 10;
		}
		@include mid {
			transition: all ease-out .3s;
			height: 0;
			max-height: 0;
			opacity: 0;
			visibility: hidden;
			overflow: hidden;
			position: absolute;
			left: 0;
			width: 220px;
			margin-top: -20px;
			&:before {
				content: "";
				height: 0;
				width: 0;
				border-bottom: 10px solid #FFF;
				border-left: 10px solid transparent;
				border-right: 10px solid transparent;
				position: relative;
				top: -8px;
				left: 25px;
			}
			&:after {
				content: "";
				display: block;
				width: 100%;
				height: calc(100% - 30px);
				box-shadow: 0 0 5px rgba(0,0,0,0.4);
				position: absolute;
				bottom: 0;
				left: 0;
			}
		}	
	}
	ul.menu ul > li {
		a {
			display: block;
			color: rgba($color_menu_link,.75);
		}
		@include mid {
			background: #FFF;
			border-top: 1px dotted rgba(0,0,0,0.15);
			a {
				padding: 8px 15px;
				font-size: 14px;
			}
		}
	}
	ul.menu ul > li:hover > a,
	ul.menu ul > li.current_page_item > a {
		color: $primary;
	}
	ul.menu ul > li:first-child {
		border-top: none;
	}
	ul.menu li:hover {
		@include mid {
			ul {
				height: auto;
				max-height: 100vh;
				opacity: 1;
				visibility: visible;
				overflow: visible;
				margin-top: -12px;
			}
		}
	}
}

body.nobanner {
