		margin-top: 45px;
	}
	h1.entry-title {
		margin: 0 0 30px;
		text-align: center;
		color: $primary;
	}
}

.menu-toggle {
	display: block;
	-webkit-appearance: none;
	outline: none;
	box-shadow: none;
	text-shadow: none;
	background: #FFF;
	position: absolute;
	top: 15px;
	right: 20px;
	width: 50px;
	height: 50px;
	padding: 6px;
	border: none;
	@include mid {
		display: none;
	}
	span.bar {
		position: relative;
	}
	span.bar, span.bar:before, span.bar:after {
		display: block;
		width: 100%;
		height: 3px;
		background: $primary;
		transition: all ease-in-out .3s;
	}
	span.bar:before, span.bar:after {
		content:"";
		position: absolute;
		left: 0;
	}
	span.bar:before {
		top: -10px;
	}
	span.bar:after {
		top: 10px;
	}
	&.open {
		span.bar {
			background: transparent;
		}
		span.bar:before, span.bar:after {
			background: $primary;
		}
		span.bar:before {
			transform: translateY(10px) rotate(45deg);
		}
		span.bar:after {
			transform: translateY(-10px) rotate(-45deg);
		}
	}
}

@media screen and (max-width: $midwidth - 1px) {
	.main-menu-wrap {
		height: 0px;
		max-height: 0;
		overflow: hidden;
	}
	.main-navigation ul.menu {
		padding-top: 10px;
		border-bottom: 5px solid $primary;
	}
	.main-navigation ul.menu > li {
		width: 100%;
		border-top: 1px solid rgba(0,0,0,.15);
	}
	.main-navigation ul.menu > li > a {
		font-size: 20px;
	}
	ul.menu ul > li > a {
		padding: 8px 35px;
		font-size: 16px;
	}
	ul.menu ul {
		margin-bottom: 16px;
	}
	.open {
		.main-menu-wrap {
			max-height: 100%;
			height: auto;
			overflow: visible;
		}
	}
}

