/* Slideshow */
.swiper-container {
	overflow: hidden;
}
.swiper-container,
.swiper-wrapper {
	width: 100%;
	height: 350px;
	@include tab {
		height: 450px;
	}
	@include mid {
		height: 600px;
	}
	@include desk {
		height: 750px;
	}
}
#slideshow.swiper-container {
	width: 100%;
	height: 100%;
	.swiper-slide {
		float: none!important;
		background-size: cover;
		background-position: center;
		img {
			width: 100%;
			height: auto;
			float: left;
		}
	}
	.slideCaption {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		text-align: center;
		&:before {
			content:"";
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
		.wrapper {
			position: relative;
			z-index: 10;
			padding-left: 30px;
			padding-right: 30px;
			@include tab {
				padding-left: 8%;
				padding-right: 8%;
			}
			@include mid {
				padding-left: 12%;
				padding-right: 12%;
			}
			@include desk {
				padding-left: 14%;
				padding-right: 14%;
			}
		}
	}
	.caption {
		font-size: 1.7rem;
		font-weight: 400;
		line-height: 1.2;
		color: #FFF;
		padding-top: 40px;
		text-shadow: 1px 1px 3px rgba(0,0,0,0.2);
		@include tab {
			padding-top: 5%;
			font-size: 2.5rem;
		}
		@include mid {
			padding-top: 6%;
		}
	}
	.slideBtn {
		a.sbtn {
			text-decoration: none;
			display: inline-block;
			color: #a11319;
			font-size: 15px;
			line-height: 1.2;
			padding: 4px 12px 2px;
			border: 2px solid $primary;
			border-radius: 3px;
			margin-top: 10px;
			background: $primary;
			color: #FFF;

			@include tab {
				padding: 6px 15px 5px;
				font-size: 18px;
			}
			&:hover {
				background: transparent;
				color: $primary;
			}
		}	
	}
	.swiper-button-next,
	.swiper-button-prev {
		display: none;
	}
	.swiper-pagination-bullet {
		width: 10px;
		height: 10px;
		background: #FFF;
		opacity: 0.45;
		&.swiper-pagination-bullet-active {
			opacity: 1;
		}
	}
}

