#loaderOverlay {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 3000;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.45);
	&.show {
		@include flexgroup-center();
	}
}
.loaderSpin,
.loaderSpin:after {
  border-radius: 50%;
  width: 5em;
  height: 5em;
}
.loaderSpin {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: .44em solid rgba(255, 255, 255, 0.2);
  border-right: .44em solid rgba(255, 255, 255, 0.2);
  border-bottom: .44em solid rgba(255, 255, 255, 0.2);
  border-left: .44em solid #ffffff;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}



